
 @font-face {
  font-family: "Mada";
  src: url(../font/Mada-VariableFont_wght.ttf) format("truetype");
  font-weight: 100 1000;
  font-stretch: 0% 200%;
}

:root {
  --_colors---off-white: #f6fafe;
  --_colors---text--text-muted: #8a5d7c;
  --_colors---blue: #1fd1be;
  --_colors---main-dark: #11010c;
  --_colors---dark-2: #491444;
  --_colors---text--text-link: #ee1c81;
  --_colors---card-background: #1d0a17;
  --_colors---yellow: #f4bc1f;
  --_colors---dark-1: #2e0621;
  --_colors---dark-3: #8e4287;
  --_colors---dark-purple: #3d2d76;
  --_colors---red: #f95e5e;
  --bg-color: #11010c;
}

body {
  margin: 0;
  background-color: var(--bg-color);
  font-family: Mada, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: var(--_colors---off-white);
  font-size: 18px;
  line-height: 140%;
}

.w-layout-grid {
  grid-row-gap: 16px;
  grid-column-gap: 16px;
  grid-template-rows: auto auto;
  grid-template-columns: 1fr 1fr;
  grid-auto-columns: 1fr;
  display: grid;
}

.main-container {
  max-width: 1440px;
  height: 100%;
  min-height: 95vh;
  margin-left: auto;
  margin-right: auto;
  padding-left: 5%;
  padding-right: 5%;
  position: relative;
}

.home-hero-grid {
  grid-column-gap: 4rem;
  grid-template-rows: auto;
  grid-template-columns: 1fr 1fr;
  grid-auto-columns: 1fr;
  display: grid;
}

.text-color-white {
  color: #f6fafe;
}

.name-form_wrapper {
  background-color: #350c28;
  border-radius: 10rem;
  justify-content: space-between;
  align-items: center;
  padding: .5rem .5rem .5rem 1rem;
  display: flex;
}

.name-form_wrapper.user-exists {
  background-color: transparent;
  border-radius: 0;
  padding: 0;
}

.input:focus {
  outline: none;
  border:none;
  background-image:none;
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  box-shadow: none;  
}

.form-input-field {
  color: var(--_colors---off-white);
  background-color: #0000;
  border: 1px #000;
  margin-bottom: 0;
  outline: none;
  border:none;
  background-image:none;
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  box-shadow: none;  
}

.form-input-field::placeholder {
  color: var(--_colors---text--text-muted);
  font-size: 18px;
}

.name-form-block {
  flex: 1;
  margin-bottom: 0;
}

.button {
  background-color: var(--_colors---blue);
  color: var(--_colors---main-dark);
  flex: none;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10rem;
  padding: .5rem 1rem;
  text-decoration: none;
  transition: all .3s;
}

.button:hover {
  color: var(--_colors---dark-2);
  box-shadow: inset -20px 0 10px 12px #c6fff954;
}

.button.is-icon {
  grid-column-gap: .5rem;
  justify-content: center;
  align-items: center;
  display: inline-flex;
}

.button.is-danger {
background-color: var(--_colors---dark-2);
color: var(--_colors---red);
}

.button.is-danger:hover {
background-color: var(--_colors---dark-2);
color: var(--_colors---red);
box-shadow: inset -20px 0 10px 2px var(--_colors---dark-2);
}

.home-hero-content-left {
  flex-flow: column;
  justify-content: center;
  align-items: stretch;
  padding-top: 6em;
  padding-bottom: 6em;
  display: flex;
  z-index: 4;
}

.home-hero-image_wrapper {
  flex-flow: row;
  justify-content: center;
  align-items: stretch;
  display: flex;
}



.is-first-section {
  padding-top: 6rem;
}

.spacer-small {
  height: 1rem;
}

.spacer-medium {
  height: 2rem;
}

.text-color-muted {
  color: var(--_colors---text--text-muted);
}

.text-style-link {
  color: var(--_colors---text--text-link);
}

.text-size-tiny {
  font-size: 14px;
  line-height: 120%;
}

.footer {
  justify-content: space-between;
  padding-top: 6rem;
  padding-bottom: 2rem;
  display: flex;
}

.text-size-small {
  font-size: 16px;
  line-height: 120%;
}

.navbar {
  z-index: 5;
  -webkit-backdrop-filter: blur(20px);
  backdrop-filter: blur(20px);
  background-color: #1d0a1799;
  border-radius: 6rem;
  justify-content: space-between;
  align-items: center;
  max-width: 90rem;
  margin-top: 1rem;
  margin-left: auto;
  margin-right: auto;
  padding: 1rem 1rem 1rem 2rem;
  display: flex;
  position: fixed;
  inset: 0% 0% auto;
}

.navbar-button_wrapper {
  justify-content: flex-start;
  align-items: center;
  display: flex;
}

.navbar-name_wrapper {
  flex: 1;
  justify-content: space-between;
  margin-right: 1rem;
  display: flex;
}

.navbar-logo {
  width: 150px;
}

.main-content-grid {
  grid-column-gap: 4rem;
  grid-row-gap: 2rem;
  grid-template-rows: auto;
}

.main-content-column {
  padding-top: 6em;
  padding-bottom: 6em;
}

.history-day-title {
  font-size: 1.5rem;
}

.history-day-entry-card {
  background-color: var(--_colors---card-background);
  color: var(--_colors---main-dark);
  display: flex;
  flex-direction: column;
  border-radius: 1.25rem;
  padding: 1rem;
  margin-bottom: 1rem;
}
.history-day-entry_container {
width: 100%;
display: flex;
flex-direction: column;
}

.reset-database {
  background-color: var(--_colors---card-background);
  color: var(--_colors---text--text-link);
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  border-radius: 1.25rem;
  padding: 1rem;
  margin-bottom: 1rem;
  box-sizing: border-box;
}

.history-day-entry-pill_container {
  grid-column-gap: .85rem;
  grid-row-gap: .85rem;
  flex-flow: wrap;
  align-content: flex-start;
  display: flex;
}

.history-day-entry-pill {
  grid-column-gap: .5rem;
  grid-row-gap: .5rem;
  background-color: var(--_colors---yellow);
  border-radius: 6rem;
  justify-content: center;
  align-items: center;
  padding: .25rem 1rem;
  display: flex;
}

.history-day-entry-pill.removable {
  padding-right: .25rem;
}

.date-form_wrapper {
  background-color: #350c28;
  border-radius: 10rem;
  justify-content: space-between;
  align-items: center;
  width: 50%;
  padding: .5rem 1rem;
  display: flex;
}

.export-form-wrapper {
  flex-flow: column;
  align-items: flex-start;
  display: flex;
}



.bubbles-container {
  z-index: 1;
  width: 100vw;
  height: 100vh;
  position: absolute;
  inset: 0%;
  overflow: hidden;
}

.bottom-bar {
  z-index: 5;
  -webkit-backdrop-filter: blur(20px);
  backdrop-filter: blur(20px);
  background-color: #1d0a1799;
  border-radius: 6rem;
  justify-content: space-between;
  align-items: center;
  max-width: 50rem;
  margin-left: auto;
  margin-right: auto;
  padding: 1rem 1rem 1rem 2rem;
  display: flex;
  position: fixed;
  inset: auto 0% 3rem;
}

.selected-emotions-container {
  grid-column-gap: .5rem;
  grid-row-gap: .5rem;
  flex-flow: wrap;
  justify-content: flex-start;
  align-items: flex-start;
  display: flex;
}

@media screen and (max-width: 991px) {
  .home-hero-grid {
    grid-template-columns: 1fr .5fr;
  }


  .home-hero-image {
    background-color: initial;
    left: -5rem;
    position: absolute;
    right: auto;
    top: -10%;
}

  .main-content-grid {
    grid-column-gap: 2rem;
  }
}

@media screen and (max-width: 767px) {
  body {
    font-size: 16px;
  }


  .home-hero-grid {
    grid-template-columns: 1fr;
  }

  .navbar {
    padding: .5rem .5rem .5rem 1.5rem;
  }

  .navbar-logo {
    min-width: 130px;
    max-width: 30%;
  }

  .main-content-grid {
    grid-column-gap: 0rem;
    grid-row-gap: 0rem;
    grid-template-columns: 1fr;
  }

  .main-content-column {
    padding-top: 2em;
  }
}

@media screen and (max-width: 479px) {
  .button.is-icon {
    flex: none;
  }

  .navbar-name_wrapper {
    flex: 0 auto;
    display: block;
  }

  .navbar-logo {
    min-width: 130px;
  }

  .export-form-wrapper {
    align-items: stretch;
  }
}


.emotion-text {
  color: var(--bg-color);
  font-weight: 800;
  font-size: 1.125rem;
  pointer-events: none;
}

.emotion-text-description {
  color: var(--bg-color);
  font-weight: 500;
  font-size: 1rem;
  pointer-events: none;
}


